import React from 'react';
import styles from '../About/About.module.css';
import CircularProgress from '@material-ui/core/CircularProgress';
import {Octokit} from '@octokit/rest';

const octokit = new Octokit();

class About extends React.Component {
	state = {
		isLoading: true,
		repolList: [],
		nameUser: [],
		infoUser: [],
		avatarUser: [],
		isError: false,
		error: {},
		firstRepo: 0,
		lastRepo: 1
	};

	componentDidMount() {
		octokit.repos.listForUser({
			username: 'AnnaSiawa'
		}).then(({ data }) => { 
			this.setState({
				repolList: data,
				isLoading: false
			});
		}).catch(error => {
			this.setState({
				isError: true,
				error: error
			})
		});


		octokit.users.getByUsername({
			username: 'AnnaSiawa'
		}).then((response) => {
			this.setState({
				nameUser: response.data.login,
				avatarUser: response.data.avatar_url,
				infoUser: response.data.bio,
				isLoading: false
			})
		}).catch(error => {
			this.setState({
				isError: true,
				error: 'Ошибка:' + error
			})
		});
	};

	forwardPage = () => {
		if (this.state.lastRepo < this.state.repolList.length) {
			this.setState( state => ({
				firstRepo: state.firstRepo + 1,
				lastRepo: state.lastRepo + 1
			}));
		}
	};

	backPage = () => {
		if (this.state.firstRepo !== 0) {
			this.setState( state => ({
				firstRepo: state.firstRepo - 1,
				lastRepo: state.lastRepo - 1
			}));
		}
	};

	render () {
		const { isLoading, repolList, nameUser, infoUser, avatarUser, isError, error, firstRepo, lastRepo } = this.state;
		const repoListInPage = repolList.slice(firstRepo, lastRepo);
		return (
			<div className={styles.wrap}>
				{ isLoading ? <div className={styles.prelouder}><CircularProgress color="secondary" /></div> :
					<div>
						<h2 className={styles.name}>My name is { nameUser }</h2>
						<p>{ infoUser }</p>
						<div className={styles.email}>
							<div>
								<img className={styles.email__img} alt='' src="data:image/png;base64," />
							</div>
							<div><a className={styles.email__link} href='mailto:anya.shurshalova@mail.ru'>anya.shurshalova@mail.ru</a></div>
						</div>
						<div className={styles.disc}>
							<div className={styles.avatar}>
								<img className={styles.avatar__img} alt='' src={avatarUser} />
							</div>
							<div className={styles.contact}>
								<div className={styles.contact__img}>		
									<a href='https://github.com/AnnaSiawa'><img alt='' src="data:image/svg+xml;base64," /></a>
								</div>
								<div className={styles.contact__img}>
									<a href='https://www.instagram.com/annakucheryavenkova?r=nametag'><img alt='' src="data:image/svg+xml;base64," /></a>
								</div>
								<div className={styles.contact__img}>
									<a href='https://vk.com/anya.kucheryavenkova'><img alt='' src="data:image/svg+xml;base64," /></a>
								</div>
							</div>
		 				</div>
		 				<h1 className={styles.title}>My repositories</h1>		
		 				{!isLoading && <> 
					 					<ol className={styles.list}>
					 						{repoListInPage.map(repo => (<a className={styles.link} key={repo.id} href={repo.html_url}>
					 													<li className={styles.item}>
										 									{repo.name}
					 													</li>
					 													<p className={styles.language}>
					 														Language: {repo.language}
					 													</p>
					 												</a>))}
					 					</ol>
					 					<div className={styles.wrap__arrow}>
						 					<div 
						 						className={styles.arrow1}
						 						onClick={this.backPage}>
						 							<img className={styles.arrow1__img} src="https://w7.pngwing.com/pngs/544/324/png-transparent-kool-aid-neuhauserhof-merano-business-line-arrow-miscellaneous-blue-angle.png" alt=""></img>
						 					</div>
						 					<div 
						 						className={styles.arrow2}
						 						onClick={this.forwardPage}>
						 							<img className={styles.arrow2__img} src="https://w7.pngwing.com/pngs/544/324/png-transparent-kool-aid-neuhauserhof-merano-business-line-arrow-miscellaneous-blue-angle.png" alt=""></img>
						 					</div>	
					 					</div>
		 							   </>
		 			}
	 				</div>}
 			</div>
 		);
	}
};

export default About;
